<template>
    <div class="schedule">
      <el-dialog title="" :visible.sync="dialogVisible" width="45%" :before-close="clearData">
        <el-form ref="form" :model="form" :inline="false" label-width="90px" label-position="right" size="medium">
          <el-form-item label=":">
            <el-input v-model="form.schedule_id" :placeholder="dialogType === 1 ? form.schedule_id : ''" :disabled="dialogType === 1"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <!-- <el-input v-model="form.doctor_id" placeholder=""></el-input> -->
            <el-select v-model="form.doctor_id" placeholder="" style="width: 140px" size="small">
            <el-option
                    v-for="item in docList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.doctor_id">
            </el-option>
          </el-select>
          </el-form-item>
          <el-form-item label=":">
            <el-date-picker v-model="form.date" placeholder="" type="date" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="form.shift_time" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":">
            <!-- <el-input v-model="form.department_id" placeholder=""></el-input> -->
          <el-select v-model="form.department_id" placeholder="" style="width: 140px" size="small">
            <el-option
                    v-for="item in dptList"
                    :key="item.department_id"
                    :label="item.department_name"
                    :value="item.department_id">
            </el-option>
          </el-select>
          </el-form-item>
          <el-form-item label=":" >

            <el-select v-model="form.is_available" placeholder="" style="width: 140px" size="small">
              <el-option
                      label=""
                      value="1">
              </el-option>
              <el-option
                      label=""
                      value="0">
              </el-option>
          </el-select>

          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="form.visit_count" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="form.remarks" type="textarea" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="clearData"></el-button>
          <el-button type="primary" @click="submit"></el-button>
        </span>
      </el-dialog>
  
      <div class="schedule-header" style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
        <div>
          <el-button type="primary" @click="showDialog"></el-button>
          <el-button type="warning" @click="exportExcel"></el-button>
        </div>
        <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
          <el-form-item>
            <el-input v-model="searchForm.schedule_id" placeholder=""></el-input>
            
          </el-form-item>
          <el-form-item>
            <el-input v-model="searchForm.doctor_id" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handleSearch"></el-button>
          </el-form-item>
        </el-form>
      </div>
  
      <div class="table-list" style="height: 80vh; background: #fff;">
        <el-table :data="pageTicket" style="width: 100%" height="85%" id="schedule_list">
          <el-table-column prop="schedule_id" label="" min-width="100"></el-table-column>
          <el-table-column prop="name" label="" min-width="100"></el-table-column>
          <el-table-column prop="date" label="" min-width="150"></el-table-column>
          <el-table-column prop="shift_time" label="" min-width="100"></el-table-column>
          <el-table-column prop="department_name" label="" min-width="100"></el-table-column>
          <el-table-column label="" min-width="100">
            <template slot-scope="scope">
              <span>{{ scope.row.is_available === 1 ? '' : '' }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="visit_count" label="" min-width="100"></el-table-column>
          <el-table-column prop="remarks" label="" min-width="200"></el-table-column>
          <el-table-column label="" min-width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="background: #fff; float: right;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { getScheduleList, delSchedule, updateSchedule, insertSchedule, selectSchedule, getDocList, getDeptList } from "@/api/index";
  import FileSaver from 'file-saver';
  import XLSX from "xlsx";
  
  export default {
    data() {
      return {
        docList: [],
        dptList: [],
        scheduleList: [],
        dialogType: 0, // 0 -   1 - 
        dialogVisible: false,
        form: {
          schedule_id: "",
          doctor_id: "",
          date: "",
          shift_time: "",
          department_id: "",
          is_available: "",
          visit_count: "",
          remarks: "",
          name:"",
          department_name:'',
        },
        searchForm: {
          schedule_id: "",
          doctor_id: ""
        },
        currentPage: 1,
        pageSize: 10,
        total: 0,
        pageTicket: []
      };
    },
    created() {
      this.getData();
    },
    mounted(){
      getDocList().then((res) => {
        this.docList = res.data.data
      })
      getDeptList().then((res) => {
        this.dptList = res.data.data
        console.log("dptList:", this.dptList)
      }).catch((err)=>{
        console.log("dplist err", err)
      })
    },
    methods: {
      getData() {
        getScheduleList().then((res) => {
            this.scheduleList = res.data.data;
            this.total = this.scheduleList === undefined ? 0 : this.scheduleList.length ;
            console.log("", this.scheduleList)
            this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
        });
      },
      showDialog() {
        this.dialogType = 0;
        this.dialogVisible = true;
        this.form = {}
      },
      clearData() {
        this.dialogVisible = false;
        this.$refs.form.resetFields();
      },
      handleEdit(index, row) {
        this.dialogType = 1;
        this.dialogVisible = true;
        this.form = { ...row };
      },
      handleDelete(index, row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.delete(row)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      delete(row){
        delSchedule(row).then(() => {
            this.getData()
        })
      },
      submit() {
        if (this.dialogType === 0) {
          insertSchedule(this.form).then((res) => {
              this.getData();
              this.dialogVisible = false;
          });
        } else {
          updateSchedule(this.form).then((res) => {
              this.getData();
              this.dialogVisible = false;
          });
        }
      },
      handleSearch() {
        selectSchedule(this.searchForm).then((res) => {
            this.scheduleList = res.data.data;
            this.total = this.scheduleList.length;
            this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
        });
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.pageTicket = this.scheduleList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
      },
      exportExcel() {
        const data = this.scheduleList.map((item) => ({
          : item.schedule_id,
          : item.doctor_id,
          : item.date,
          : item.shift_time,
          : item.department_id,
          : item.is_available ? "" : "",
          : item.visit_count,
          : item.remarks
        }));
        const ws = XLSX.utils.json_to_sheet(data);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "");
        const wbout = XLSX.write(wb, { bookType: "xlsx", type: "array" });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream" }),
            ".xlsx"
          );
        } catch (e) {
          console.log(e, wbout);
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .schedule {
    padding: 20px;
  }
  </style>
  