<template>
  <div class="DOC">

    <!-- <button @click="handleClick()"></button> -->
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="45%" :before-close="clearData">


      <!--  -->
      <el-form ref="form" :model="form" :inline="true" label-width="90px" label-position="right" size="medium">
        <el-form-item label=":">
          <el-input v-model="form.doctor_id" :placeholder="diglogType == 1 ? form.doctor_id : ''" :disabled="diglogType == 1"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.job_number" :placeholder="diglogType == 1 ?  form.job_number : ''" :disabled="diglogType == 1"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.password" placeholder=""></el-input>
        </el-form-item>
        
        <el-form-item label=":">
          <el-select v-model="form.professional_title_id" placeholder="" style="width: 140px" size="small">
            <el-option
                    v-for="item in titlesList"
                    :key="item.id"
                    :label="item.title_name"
                    :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        
        


        <el-form-item label=":">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.introduction" placeholder="" type="textarea" resize="none"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.registration_fee" placeholder=""></el-input>
        </el-form-item>
        <!-- <el-form-item label=":" prop="password">
          <el-select v-model="form.password" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="">
          <el-col :span="11" style="width: 80%;">
            <el-date-picker type="date" placeholder="" v-model="form.entry_date" style="width: 100%;"
              value-format="yyyy-MM-dd"></el-date-picker>

              <!-- <el-date-picker
      v-model="value1"
      type="datetime"
      placeholder="">
    </el-date-picker>  -->
          </el-col>
        </el-form-item>
        <!-- <el-form-item label=":" prop="addr">
          <el-input v-model="form.addr" placeholder=""></el-input>
        </el-form-item> -->
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="clearData"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>

    <!--  -  -  -->
    <div class="users-header"
      style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
      <div>
        <el-button type="primary" @click="showDialog">+ </el-button>
        <el-button type="warning" @click="exportExcel"> </el-button>
      </div>

      <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
        <el-form-item>
          <el-input v-model="searchForm.doctor_id" placeholder="id"></el-input>
        </el-form-item>
        
        <el-form-item>
          <el-input v-model="searchForm.name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>


    <!--  -->
    <div class="table-list" style="height: 80vh ; background: #fff;">
      <el-table :data="pageTicket" style="width: 100%" height="85%" id="doc_list">

        <el-table-column prop="doctor_id" label="" width="50">
        </el-table-column>

        <el-table-column prop="job_number" label="" width="80">
        </el-table-column>

        <el-table-column prop="password" label="" width="80">
        </el-table-column>

        <el-table-column prop="name" label="" min-width="80">
        </el-table-column>

        <el-table-column prop="avatar" label="" min-width="100">
        </el-table-column>

        <el-table-column prop="phone" label="" min-width="120">
        </el-table-column>

        <el-table-column prop="email" label="" min-width="120">
        </el-table-column>

        <el-table-column prop="introduction" label="" min-width="300">
        </el-table-column>

        <el-table-column prop="registration_fee" label="" min-width="100">
        </el-table-column>

        <el-table-column prop="title_name" label="" min-width="100">
        </el-table-column>


        <el-table-column prop="entry_date" label="" min-width="150">
        </el-table-column>

        <el-table-column label="" min-width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="background: #fff; float: right;">
        <!-- <el-pagination layout="prev, pager, next" :total="total" @current-change="handlePage" :page-size="20">
        </el-pagination> -->
        <el-pagination
          @size-change="handleSizeChange" 
          @current-change="handleCurrentChange" 
          :current-page="currentPage"  
          :page-sizes="[5, 10, 20]"  
          :page-size="pagesize"  
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">   //
        </el-pagination>

      </div>
    </div>


  </div>
</template>

<script>

import { getImages, getAdminList, selectTitle, getDocList, delDoc, updateDoc, insertDoc, selectDoc } from "@/api/index"
import FileSaver from 'file-saver';
import XLSX from "xlsx"


export default {
  data() {
    return {
      titlesList: [],
      docList: [],  // 
      diglogType: 0,  // 0 -   1 - 
      dialogVisible: false,
      form: {
        doctor_id: '',
        job_number: '',
        password: '',
        name: '',
        avatar: '',
        email: '',
        introduction: '',
        registration_fee: '',
        entry_date: '',
        phone: '',
        department_id: '',
        professional_title_id: ''
      },
      searchForm:{
        doctor_id:'',
        name: '',
      },
      total: 0,
      currentPage: 1,
      pagesize: 10,
      pageTicket: [], //
    };
  },
  methods: {
    // 

    // 
    getPageInfo(){
      
      // console.log("total:" + this.total)

      this.pageTicket = []

      for(let i = (this.currentPage - 1) * this.pagesize; i < this.total; i++){
        console.log("")
        this.pageTicket.push(this.docList[i])
        // 
        if(this.pageTicket.length === this.pagesize) break;
      }
      
      console.log("")
      console.log(this.pageTicket)
    },

    // 
    handleSizeChange(size){
      this.pagesize = size
      this.getPageInfo()
    },

    // -
    handleCurrentChange(pagenum){
      this.currentPage = pagenum
      this.getPageInfo()
    },


    // 
    showDialog() {
      this.diglogType = 0
      this.dialogVisible = !this.dialogVisible
        this.form = {}
    },


    // 

    clearData() {
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    submit() {
      console.log("", this.form)
      this.$refs.form.validate((value) => {
        if (value) {
          if (this.diglogType == 0) {
            console.log("")
            this.add()
          }
          else {
            this.edit()
          }
            this.clearData()
        }
        else {
          alert(",")
        }
      })

    },
    cancel() {
      this.clearData()
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    initList() {
      getDocList().then(
        (res) => {
          this.docList = res.data.data
        
          this.total = this.docList.length
          
          this.getPageInfo()
        }
      )
    },
    handleDelete(index, row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        this.delete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleEdit(index, row) {
      // 
      this.diglogType = 1;
      this.dialogVisible = !this.dialogVisible
      // this.form = JSON.parse(JSON.stringify(row))
      this.form = { ...row }
      console.log(":" + this.form)
    },

    // 
    handleSearch(){
      this.select(this.searchForm)
    },

    // 
    select(data){
      selectDoc(data).then((res) => {
        this.docList = res.data.data
        this.total = this.docList.length
        this.getPageInfo()
      }).catch(
        ()=>{
          console.log("")
        }
      )
    },

    // 
    delete(row) {
      // console.log(row)
      delDoc({ doctor_id: row.doctor_id }).then(
        () => {
          this.initList()
        }
      ).catch(
        console.log("")
      )
    },

    // 
    edit() {
      updateDoc(this.form).then(
        () => {
          this.initList()
        }
      ).catch(
        () => {
        }
      )
    },

    // 
    add() {
      insertDoc(this.form).then(
        () => {
          console.log("")
          this.initList()
        }
      ).catch(
        () => {
          console.log("")
        }
      )
    },

    // 
    getTitleList(){
      selectTitle().then((res) => {
        this.titlesList = res.data.data
      })
    },

    // 
    exportExcel() {
      var xlsxParam = { raw: true };
      /*  */
      var wb = XLSX.utils.table_to_book(
        document.querySelector("#doc_list"),
        xlsxParam
      );
      /*  */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array",
      });
      try {
        FileSaver.saveAs(
          //Blob 
          //Blob JavaScript
          //File Blob blob 
          // Blob 
          new Blob([wbout], { type: "application/octet-stream" }),
          //
          ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    },

  },
  mounted() {
    this.initList()
    this.getTitleList()
  }
}
</script>

<style scoped>
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
</style>