<template>
  <div class="PATIENT">

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="45%" :before-close="clearData">
      <!--  -->
      <el-form ref="form" :model="form" :inline="true" label-width="90px" label-position="right" size="medium">
        <el-form-item label=":">
          <el-input v-model="form.patient_id" :placeholder="dialogType == 1 ? form.patient_id : ''" :disabled="dialogType == 1"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.id_card_number" :placeholder="dialogType == 1 ? form.id_card_number : ''" :disabled="dialogType == 1"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.phone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form.balance" placeholder=""></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="clearData"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>

    <!--  -  -  -->
    <div class="users-header" style="margin-bottom: 10px; display: flex; justify-content: space-between; align-items: center;">
      <div>
        <el-button type="primary" @click="showDialog">+ </el-button>
        <el-button type="warning" @click="exportExcel"></el-button>
      </div>

      <el-form :model="searchForm" :inline="true" style="margin-top: 15px">
        <el-form-item>
          <el-input v-model="searchForm.patient_id" placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="searchForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <div class="table-list" style="height: 80vh ; background: #fff;">
      <el-table :data="pageTicket" style="width: 100%" height="85%" id="patient_list" >
        <el-table-column prop="patient_id" label="" min-width="100"></el-table-column>
        <el-table-column prop="id_card_number" label="" min-width="150"></el-table-column>
        <el-table-column prop="password" label="" min-width="100"></el-table-column>
        <el-table-column prop="name" label="" min-width="100"></el-table-column>
        <el-table-column prop="avatar" label="" min-width="100"></el-table-column>
        <el-table-column prop="phone" label="" min-width="120"></el-table-column>
        <el-table-column prop="email" label="" min-width="180"></el-table-column>
        <el-table-column prop="balance" label="" min-width="100"></el-table-column>
        <el-table-column label="" min-width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="background: #fff; float: right;">
        <el-pagination
          @size-change="handleSizeChange" 
          @current-change="handleCurrentChange" 
          :current-page="currentPage"  
          :page-sizes="[5, 10, 20]"  
          :page-size="pagesize"  
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getPatientList, delPatient, updatePatient, insertPatient, selectPatient } from "@/api/index"
import FileSaver from 'file-saver';
import XLSX from "xlsx"

export default {
  data() {
    return {
      patientList: [],  // 
      dialogType: 0,  // 0 -   1 - 
      dialogVisible: false,
      form: {
        patient_id: '',
        id_card_number: '',
        password: '',
        name: '',
        avatar: '',
        phone: '',
        email: '',
        balance: '',
      },
      searchForm:{
        patient_id: '',
        name: '',
      },
      total: 0,
      currentPage: 1,
      pagesize: 10,
      pageTicket: [], //
    };
  },
  methods: {
    // 

    // 
    getPageInfo(){
      this.pageTicket = []

      for(let i = (this.currentPage - 1) * this.pagesize; i < this.total; i++){
        this.pageTicket.push(this.patientList[i])
        // 
        if(this.pageTicket.length === this.pagesize) break;
      }
    },

    // 
    handleSizeChange(size){
      this.pagesize = size
      this.getPageInfo()
    },

    // 
    handleCurrentChange(pagenum){
      this.currentPage = pagenum
      this.getPageInfo()
    },

    // 
    showDialog() {
      this.dialogType = 0
      this.dialogVisible = !this.dialogVisible
      this.form = {}
    },

    // 

    clearData() {
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    submit() {
      this.$refs.form.validate((value) => {
        if (value) {
          if (this.dialogType == 0) {
            this.add()
          }
          else {
            this.edit()
          }
          this.clearData()
        }
        else {
          alert(",")
        }
      })
    },

    // 
    initList() {
      getPatientList().then(
        (res) => {
          this.patientList = res.data.data
          this.total = this.patientList.length
          this.getPageInfo()
        }
      )
    },

    handleDelete(index, row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        this.delete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleEdit(index, row) {
      // 
      this.dialogType = 1;
      this.dialogVisible = !this.dialogVisible
      this.form = { ...row }
    },

    // 
    handleSearch(){
      this.select(this.searchForm)
    },

    // 
    select(data){
      selectPatient(data).then((res) => {
        this.initList()
      }).catch(
        ()=>{
          console.log("")
        }
      )
    },

        // 
        delete(row) {
      delPatient({ patient_id: row.patient_id }).then(() => {
        this.initList()
      }).catch(() => {
        console.log("")
      })
    },

    // 
    edit() {
      updatePatient(this.form).then(() => {
        this.initList()
      }).catch(() => {
        console.log("")
      })
    },

    // 
    add() {
      insertPatient(this.form).then(() => {
        this.initList()
      }).catch(() => {
        console.log("")
      })
    },

    // 
    initList() {
      selectPatient(this.searchForm).then((res) => {
        this.patientList = res.data.data
        console.log("patientlist", this.patientList)
        this.total = this.patientList.length
        this.getPageInfo()
      }).catch(() => {
        console.log("")
      })
    },
  
      // 
      exportExcel() {
        var xlsxParam = { raw: true };
        var wb = XLSX.utils.table_to_book(
          document.querySelector("#dept_list"),
          xlsxParam
        );
        var wbout = XLSX.write(wb, {
          bookType: "xlsx",
          bookSST: true,
          type: "array",
        });
        try {
          FileSaver.saveAs(
            new Blob([wbout], { type: "application/octet-stream" }),
            ".xlsx"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, wbout);
        }
        return wbout;
      },
  
    },
    mounted() {
      this.initList()
    }
  }
  </script>
  
  <style scoped>
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  
  .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }
  
  .button {
    padding: 0;
    float: right;
  }
  
  .image {
    width: 100%;
    display: block;
  }
  
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  
  .clearfix:after {
    clear: both
  }
  </style>
  