import axios from 'axios'
const http = axios.create({
    // 
    baseURL: '/',
    // 
    timeout: 10000
})


// 
http.interceptors.request.use(function (config) {
    // 
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// 
http.interceptors.response.use(function (response) {
    // 2xx 
    // 
    return response;
  }, function (error) {
    //  2xx 
    // 
    return Promise.reject(error);
  });


export default http